* {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding: 0;
  margin: 0;
}
body {
  background-color: #f1f5f8;
}

/* header Styling */
.container {
  max-width: 1200px;
  margin: auto;
  padding-top: 20px;
}
.container2 {
  max-width: 1200px;
  margin: auto;
  padding-top: 20px;
  text-align: center;
  margin-top: 20vh;
}
.header {
  width: 100%;
  text-align: center;
  padding: 15px;
}
.theLine {
  width: 120px;
  height: 5px;
  background-color: #2caeba;
  margin: auto;
  margin-top: 15px;
}

.mainContent {
  width: 100%;
  display: grid;
  grid-template-columns: 200px 1fr;
  padding: 10px;
}

.buttons {
  padding: 10px;
  width: 80%;
  margin: 10px auto;
}
.btn {
  width: 100%;
  border-color: transparent;
  font-size: 1.25rem;
  padding: 3px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin: auto;
  margin-top: 15px;
  background-color: inherit;
  text-transform: uppercase;
  outline-color: #f1f5f8;
  border: transparent;
  /* border-radius: 3px; */
  transition: 0.35s ease-in-out;
  box-sizing: border-box;
  border-left: 4px #f1f5f8 solid;
}
.activebtn {
  box-shadow: rgba(116, 211, 240, 0.397) 3px 3px 6px 0px inset,
    rgba(116, 211, 240, 0.397) -3px -3px 6px 1px inset;
  border-left: 4px rgba(27, 185, 233, 0.966) solid;
  color: rgb(8, 157, 202);
}
.btn:hover {
  box-shadow: rgba(116, 211, 240, 0.397) 3px 3px 6px 0px inset,
    rgba(116, 211, 240, 0.397) -3px -3px 6px 1px inset;
  border-left: 4px rgba(27, 185, 233, 0.966) solid;
}
.article {
  padding: 20px;
}
.role {
  display: grid;
  grid-template-columns: auto 1fr;
  /* grid-column-gap: 2rem; */
  align-items: center;
  column-gap: 2rem;
  margin-top: 20px;
}
.role p {
  color: #33626d;
}
.roleico {
  color: #2caeba;
}

.jopHeader {
  color: rgb(15, 37, 41);
  font-weight: 400;
  letter-spacing: 5px;
  margin-bottom: 10px;
}
.article h3 {
  padding: 0.375rem 0.75rem;
  background-color: #b3dce8b0;
  margin-bottom: 10px;
  display: inline-block;
  text-align: center;
  font-size: 16px;
  border-radius: 5px;
  font-weight: 600;
  color: #496161;
}

.date {
  word-spacing: 10px;
  color: #103540b0;
}

.btn2 {
  width: 150px;
  padding: 8px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
  border-color: transparent;
  text-transform: uppercase;
  background-color: rgb(28, 151, 160);
  border-radius: 5px;
  font-weight: 700;
  letter-spacing: 2px;
  color: white;
}
.btn2:hover {
  background-color: rgb(111, 203, 209);
  color: rgb(50, 54, 54);
}
.moreinfo {
  width: 100%;
  text-align: center;
  padding: 15px;
}
/* responsive */

@media only screen and (max-width: 900px) {
  .mainContent {
    display: block;
  }
  .buttons {
    padding: 10px;
    width: 100%;
    text-align: center;
  }
  .btn {
    width: auto;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    margin-left: 15px;
  }
  .jopHeader {
    font-size: 18px;
    letter-spacing: 3px;
  }
  .date {
    word-spacing: 3px;
  }
}
